<template>
<section id="signin" class=" d-flex align-items-center">

    <div class="container">
        <div class="row">
            <div class="col-lg-7 d-flex flex-column justify-content-center">
                <div class="signin-lgo">
                    <img src="../assets/images/workwire.png">
                </div>

            </div>

            <div class="col-lg-5 d-flex  justify-content-center align-items-center pt-4 ">

                <div class="signin-box_bx">
                    <div class="sub-text">
                        <h4> Create an account</h4>

                        <div class="all-social-dx">
                            <div>
                                <a href="#" class="alradt-acount_cx"> <img src="../assets/images/Google Logo.png"> </a>
                                <a href="#" class="alradt-acount_bx"> <img src="../assets/images/logos_facebook.png"> </a>
                                <a href="#" class="alradt-acount_dx"> <img src="../assets/images/Vector.png"> </a>

                            </div>
                        </div>
                        <!--  <h5> Choose a display name</h5> -->
                        <div class="join-workshire_bx">
                            <div class="or-line">
                                <p> OR</p>

                            </div>

                            <form v-on:submit="onSubmit" method="post" class="body">
                                <div class="form-group">
                                    <h5> Sign up with email</h5>
                                    <input type="text" class="form-control" required v-model="posts.firsttName" name="fisrtName" placeholder="First Name">
                                </div>
                                <div class="form-group">

                                    <input type="text" class="form-control" required v-model="posts.lastName" name="lastName" placeholder="Last Name">
                                </div>
                                <div class="form-group">

                                    <input type="email" class="form-control" required v-model="posts.email" name="email" placeholder="Email">
                                </div>
                                <div class="form-group">

                                    <input type="password" class="form-control" required v-model="posts.password" name="password" placeholder="Password">
                                </div>
                                <div class="form-group">

                                    <input type="tel" class="form-control" required v-model="posts.phoneNumber" name="phoneNumber" placeholder="Phone No">
                                </div>
                            </form>

                            <div class="box-join_bx">
                                <button type="submit">Submit</button>

                            </div>

                        </div>

                        <router-link to="/signin-form" class="alradt-acount" style="width: 100%;"><label>Already have an account? </label> <span>Sign in.</span> <img src="../assets/images/back-arow.png"></router-link>

                    </div>

                </div>

            </div>

        </div>

        <!-- <div class="col-lg-6 order-1 order-lg-2 hero-img" >
          <img src="image/sliderbg.png" class="img-fluid " alt="">
        </div> -->
    </div>

</section>
</template>

<script>
import { postRequest } from './ApiFunctions';


export default {
    name: "RegisterForm",
    data() {
        return {
            posts: {
                firstName: "",
                lastName: "",
                email: "",
                phoneNumber: "",
                password: ""
            }
        }
    },
    methods: {
        async onSubmit() {
            alert("hello");
            var register = await postRequest('/register', this.posts);
            console.log("register", register);

        },
    }
}
</script>

<style scoped>
/* Importing fonts from Google */
/* @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap'); */

/* Reseting */
/* * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
} */
/* 
body {
    background: linear-gradient(45deg, #ce1e53, #8f00c7);
    min-height: 100vh;
} */

/* body::-webkit-scrollbar {
    display: none;
} */

.wrapper {
    max-width: 800px;
    margin: 80px auto;
    padding: 30px 45px;
    box-shadow: 5px 25px 35px #3535356b;
    font-family: sans-serif;
    background: -webkit-linear-gradient(to right, #155799, #159957);
    background: linear-gradient(to right, #155799, #159957);
    color: whitesmoke;
}

.wrapper label {
    display: block;
    padding-bottom: 0.2rem;
    color: #161515;
    font-size: 1.3rem;
}

.wrapper .forms .row {
    padding: 0.6rem 0;
}

.wrapper .forms .row .form-control {
    box-shadow: none;
}

.wrapper .forms .option {
    position: relative;
    padding-left: 20px;
    cursor: pointer;
}

.wrapper .forms .option input {
    opacity: 0;
}

.wrapper .forms .checkmark {
    position: absolute;
    top: 1px;
    left: 0;
    height: 20px;
    width: 20px;
    border: 1px solid #bbb;
    border-radius: 50%;
}

.wrapper .forms .option input:checked~.checkmark:after {
    display: block;
}

.wrapper .forms .option:hover .checkmark {
    background: #f3f3f3;
}

.wrapper .forms .option .checkmark:after {
    content: "";
    width: 10px;
    height: 10px;
    display: block;
    background: linear-gradient(45deg, #ce1e53, #8f00c7);
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 50%;
    transform: translate(-50%, -50%) scale(0);
    transition: 300ms ease-in-out 0s;
}

.wrapper .forms .option input[type="radio"]:checked~.checkmark {
    background: #fff;
    transition: 300ms ease-in-out 0s;
}

.wrapper .forms .option input[type="radio"]:checked~.checkmark:after {
    transform: translate(-50%, -50%) scale(1);
}

#sub {
    display: block;
    width: 100%;
    border: 1px solid #ddd;
    padding: 10px;
    border-radius: 5px;
    color: #333;
}

#sub:focus {
    outline: none;
}

@media(max-width: 768.5px) {
    .wrapper {
        margin: 30px;
    }

    .wrapper .form .row {
        padding: 0;
    }
}

@media(max-width: 400px) {
    .wrapper {
        padding: 25px;
        margin: 20px;
    }
}

.field {
    margin: 0;
    padding: 4px;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

button {
    background-color: #030804;
    color: white;
    padding: 14px 20px;
    border-radius: 5px;
    margin: 7px 0;
    width: 100%;
    font-size: 18px;
}

button:hover {
    opacity: 0.6;
    cursor: pointer;
}
</style>



